.PongButton {
  background-color: white;
  color: #4285F4;
  font-size: 10vh;
  border-radius: 5px;
  padding: 2px 10px 2px 10px;
  margin: 20px auto;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.PongButton-align-bottom {
  position: absolute;
  bottom: 0px;
  left: 50%;
  -webkit-transform: translate(-50%);
      -ms-transform: translate(-50%);
          transform: translate(-50%);
}

.PongButton-small {
  font-size: 3vh;
}

.PongButton-medium {
  font-size: 6vh;
}

.PongButton-back {
  position: absolute;
  top: 0px;
  left: 20px;
}



